.light{
  background-color: white;
}
.dark{
  background-color: rgb(24, 24, 24);
  color: white;

}

@media screen and (max-width: 600px) {
  .homepage-wrapper-light{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    margin-left: 4rem;
    margin-right: 4rem;
    justify-content: space-around;
  }
  .homepage-wrapper-dark{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    margin-left: 4rem;
    margin-right: 4rem;
    justify-content: space-around;
  }
}
@media screen and (min-width: 601px) {
  .homepage-wrapper-light{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    margin-left: 4rem;
    margin-right: 4rem;
    justify-content: space-around;
  }
  .homepage-wrapper-dark{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    margin-left: 4rem;
    margin-right: 4rem;
    justify-content: space-around;
  }
}
.navlink-homepage-light{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: x-large;
  font-weight: 200;
  border: 1px solid black;
  border-radius: 8px;
  width: 14rem;
  height: 7rem;
  margin-bottom: 1rem;
}
.navlink-homepage-dark{
  display: flex;
  margin-bottom: 1rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: x-large;
  font-weight: 200;
  border: 1px solid black;
  border-radius: 8px;
  width: 14rem;
  height: 7rem;
}
.navlink-homepage-dark p{
  font-size: medium;
}
.navlink-homepage-light p{
  font-size: medium;
}
.navlink-back{
  display: flex;
  align-items: center;
  appearance: none;
  text-decoration-color: none;
  text-decoration: none;
  color: rgb(52,134,246);
  font-size: 18px;
  margin: 6px;
}
.navlink-back button{
  appearance: none;
  background-color: transparent;
  border: none;
}
.header-light{
  height: 130px;
  display: flex;
  justify-content: center;
  color: white;
  align-items: center;
  top: 0rem;
  width: 100%;
  background-color: rgb(237, 237, 237);
  box-shadow: 1px 1px 10px rgb(161, 161, 161);
  color: black;
}
.header-dark{
  height: 130px;
  display: flex;
  justify-content: center;
  color: white;
  align-items: center;
  top: 0rem;
  width: 100%;
  box-shadow: 1px 1px 10px rgb(0, 0, 0);
  background-color: rgb(37, 37, 37);
}
.App h1{
  font-weight: 10;
  text-align: center;
  font-size:3rem;
}
.input-search-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.input-search-light{
  font-size: 1rem;
  width: 7rem;
  height: 1.5rem;
  border-radius: 6px;
  border: none;
  box-shadow: 0px 0px 4px rgb(95, 95, 95);
  background-color: rgb(237, 237, 237);
  padding: 4px 4px 4px 10px;
  -webkit-transition: width 0.2s ease-in-out;
  transition: width 0.2s ease-in-out; 
}
.input-search-dark{
  font-size: 1rem;
  width: 7rem;
  height: 1.5rem;
  border-radius: 6px;
  border: none;
  color: white;
  box-shadow: 0px 0px 4px rgb(0, 0, 0);
  background-color: rgb(37, 37, 37);
  padding: 4px 4px 4px 10px;
  -webkit-transition: width 0.2s ease-in-out;
  transition: width 0.2s ease-in-out; 
}
@media screen and (max-width: 500px) {
  .input-search-light:focus,.input-search-dark:focus{
    width: 14em;
  }
}
@media screen and (min-width: 501px) {
  .input-search-light:focus,.input-search-dark:focus{
    width: 14rem;
  }
}


@media screen and (max-width: 499px) {
  .todo-list-light{
    font-size: 1rem;
    height: 130px;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
    margin-right: 3rem;
    border-radius: 5px;
    background-color: rgb(237, 237, 237);
    box-shadow: 1px 1px 5px rgb(161, 161, 161);
  }
  .todo-list-dark{
    font-size: 1rem;
    height: 130px;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
    margin-right: 3rem;
    border-radius: 5px;
    background-color: rgb(37, 37, 37);
    box-shadow: 0px 0px 4px rgb(0, 0, 0);
    color: white;
  }
}
@media screen and (min-width: 500px) {
  .todo-list-light{
    font-size: 1rem;
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
    margin-right: 3rem;
    border-radius: 5px;
    background-color: rgb(237, 237, 237);
    box-shadow: 1px 1px 5px rgb(161, 161, 161);
  }
  .todo-list-dark{
    font-size: 1rem;
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
    margin-right: 3rem;
    border-radius: 5px;
    background-color: rgb(37, 37, 37);
    box-shadow: 0px 0px 4px rgb(0, 0, 0);
    color: white;
  }
}

.todo-item-light{
  text-overflow:ellipsis;
  overflow-x:scroll;
  overflow-y:scroll;
  height: auto;
  width: 90%;
  margin-left: 1rem;
  font-weight: 300;
  font-size: 1.2rem;
  
}
.todo-item-dark{
  text-overflow: ellipsis;
  overflow:hidden;
  width: 90%;
  margin-left: 1rem;
  font-weight: 300;
  font-size: 1.2rem;
  
}
.todo-item-input-light{
  background-color: rgb(237, 237, 237);
  border-bottom: 1px solid black;
  border-left: none;
  border-right: none;
  border-top: none;
  width: 90%;
  margin-left: 1rem;
  font-weight: 300;
  font-size: 1.2rem;
}
.todo-item-input-dark{
  background-color: rgb(37, 37, 37);
  color: white;
  border-bottom: 1px solid rgb(255, 255, 255);
  border-left: none;
  border-right: none;
  border-top: none;
  width: 90%;
  margin-left: 1rem;
  font-weight: 300;
  font-size: 1.2rem;
  
}
.checkmark{
  width: 20px;
  height: 20px;
  margin-right: 1rem;
}


.btn-all-light{
  background: transparent;
  border: none;
  font-size: 1.2rem;
  margin-right: 1rem;
}
.btn-all-dark{
  background: transparent;

  border: none;
  font-size: 1.2rem;
  margin-right: 1rem;
}
.input-add-wrapper-light{
  display: flex;
  justify-content: center;
  margin-top: 1rem;
  width: 100%;
}
.input-add-wrapper-dark{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  width: 100%;
}
.input-add-light{
  background-color: rgb(237, 237, 237);
  font-size: 1rem;
  width: 7rem;
  height: 1.5rem;
  border-radius: 6px;
  border: none;
  box-shadow: 0px 0px 4px rgb(95, 95, 95);
  padding: 4px 4px 4px 10px;

}
.input-add-dark{
  font-size: 1rem;
  width: 7rem;
  height: 1.5rem;
  border-radius: 6px;
  border: none;
  color: white;
  box-shadow: 0px 0px 4px rgb(0, 0, 0);
  background-color: rgb(37, 37, 37);
  padding: 4px 4px 4px 10px;

}
.form{
  display: flex;
  justify-content: center;
  align-items: center;
}
.button-add-light{
  -webkit-appearance:none;
  font-size: 1rem;
  width: 7rem;
  height: 2rem;
  border: none;
  border-radius: 6px;
  background-color: rgb(52,134,246);
  color: black;
  margin-left: 10px;
}
.button-add-dark{
  -webkit-appearance:none;
  font-size: 1rem;
  width: 7rem;
  height: 2rem;
  border: none;
  border-radius: 6px;
  background-color: rgb(52,134,246);
  color: white;
  margin-left: 10px;
}
